import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MatButtonToggleChange } from '@angular/material/button-toggle';
import { Router } from '@angular/router';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-flight-train-button-toggle',
    templateUrl: './flight-train-button-toggle.component.html',
    styleUrls: ['./flight-train-button-toggle.component.scss'],
})
export class FlightTrainButtonToggleComponent {
    @Input() selectedButton: 'flights' | 'trains' = 'flights';
    @Input() skeletonLoading = true;
    @Output() buttonChange: EventEmitter<string> = new EventEmitter<string>();

    constructor(private router: Router, public platformService: PlatformService) {}

    public onButtonToggleChange(option: MatButtonToggleChange): void {
        this.buttonChange.emit(option.value);
        this.router.navigate([`/results/travel/${option.value}`], {
            state: {
                extra: {
                    preserveSelection: true,
                },
                sort_by: 'bestValue',
            },
        });
    }
}
